.box{
  width: 50px;
  height: 50px;
  border: 2px solid rgb(229, 218, 218);
  margin: 2px;
  display: flex;
}
#container{
  display: flex;
  width: 100%;
  height: 50%;
  justify-content: center;
  align-items: last baseline;
  background-color: rgba(192, 147, 220, 0.684);
  padding: 30vh 0vw;
}
center{
  height: 40vh;
}
button{
  padding:2vh 3vw;
  border-radius:1px;
  background-color: rgb(133, 206, 81);
  border:2px solid rgb(133, 219, 104);
}
p{
  position: absolute;
  top:0;
  left:35%;
  color: #0c0701; 
  font-size: 6vw;
  opacity: 0.3;
}